/* eslint-disable */
import ng from 'vendor-bb-angular';

import bbStorageModuleKey, { bbStorageServiceKey } from 'lib-bb-storage-ng';
import billPayData from './data-bb-bill-pay-http';

const billPayDataModuleKey = 'data-bb-bill-pay-http-ng';

export const billPayDataKey = 'data-bb-bill-pay-http-ng:billPayData';

export default ng
  .module(billPayDataModuleKey, [
    bbStorageModuleKey,
  ])
  .provider(billPayDataKey, [() => {
    const config = {
      baseUri: '/',
    };

    return {
      setBaseUri: (baseUri) => {
        config.baseUri = baseUri;
      },
      $get: [
        '$q',
        bbStorageServiceKey,
        '$httpParamSerializer',
        /* into */
       billPayData(config),
      ],
    };
  }])
  .name;



